.sectionPhotos {
 max-width: 90rem;
 margin: 1rem auto 4rem;
 text-align: center;
 //.sectionPhotogallery
 } ////.gallery

.gallery {
 display: flex;
 justify-content: center;
 align-items: center;
 flex-wrap: wrap;

 > a {
 display: block; }

 img {
 width: 95%;
 margin-top: 1rem;
 border-radius: 4px;
 transition: all 0.2s ease-in-out;
 &:hover {
 filter: brightness(160%); } } }

@media screen and (min-width: 735px) {
 .gallery {
 flex-direction: row;
 img {
 max-width: 350px; } } }
